  <div class="orderForm_container">
    <div class="top">
      <van-image
        fit="contain"
        :src="require('../assets/image/index_top.png')"
      />
    </div>
    <div class="user-info">
      <van-field
      type="number"
        v-model="xuehao"
        label="您的学号"
        placeholder="请输入学号"
        input-align="right"
        required
      />
      <van-field
        v-model="user_name"
        label="您的姓名"
        placeholder="请输入姓名"
        input-align="right"
        required
      />
      <van-field
        v-model="id_card"
        type="text"
        label="您的身份证"
        placeholder="请输入身份证号码"
        input-align="right"
        required
      />
      <van-field
        v-model="mobile"
