      placeholder="请选择学部"
      @click="showJzOrder = true"
      is-link
      input-align="right"
      required
    />
    <van-popup v-model="showJzOrder" position="bottom">
      <van-picker
        show-toolbar
        :columns="jzOrderList"
        @confirm="jzOrderConfirm"
        @cancel="showJzOrder = false"
      />
    </van-popup>

   
    
    <div class="order-btn" @click="handleCheck">提交信息</div>
  </div>
