<script>
/* eslint-disable */
import { Toast, Dialog } from "vant";
import { townList, areaList, limitData, submitData } from "../http/api";
import dialogContent from "../assets/js/data.js";

export default {
  name: "orderForm",
  // props: {},
  // components: {},
  data() {
    // 存放数据
    return {
      type: 1,
      dialogContent: dialogContent,
      xuehao:null,
      user_name: null,
      id_card: null,
      mobile: null,
      town: null,
      town_value: null,
      showTown: false,
      townList: [],
      remark: null,
      jz_order: null,
      jz_order_value: null,
      jzOrderList: ["康复医学部", "基础医学部","临床医学部",'护理学部','公共卫生学部','药学部','医学技术学部'],
      showJzOrder: false,
      jz_org: null,
      jz_org_value: null,
      showJz: false,
      book_date: null,
      period: null,
      activeId: 1,
      activeIndex: 0,
      showTime: false,
      nowTime: null,
    };
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 生命周期 - 创建完成
  created() {
    
   
  },
  // 生命周期 - 挂载完成
  mounted() {
  
  },

  beforeCreate() {}, // 生命周期 - 创建之前
  beforeMount() {}, // 生命周期 - 挂载之前
  beforeUpdate() {}, // 生命周期 - 更新之前
  updated() {}, // 生命周期 - 更新之后
  beforeDestroy() {}, // 生命周期 - 销毁之前
  destroyed() {}, // 生命周期 - 销毁完成
  activated() {}, // 如果页面有keep-alive缓存功能，这个函数会触发
  // 方法集合
  methods: {
    
    // 选择剂次
    jzOrderConfirm(value, index) {
      this.jz_order_value = value;
      this.jz_order = index + 1;
      console.log(this.jz_order);
      this.showJzOrder = false;
    },
    
    orgChange(picker, value, index) {
      Toast("地址：" + this.orgList[index].address);
    },
    orgConfirm(value, index) {
      this.jz_org = this.orgList[index].id;
      this.jz_org_value = value.org_name;
      console.log(this.jz_org);
      this.showJz = false;
    },
    
    
    
    
    

    handleCheck() {
      if (!this.user_name) {
        Toast("请输入姓名");
      } else if (!this.id_card) {
        Toast("请输入身份证号码");
      } else if (!this.mobile) {
        Toast("请输入手机号码");
      }  else if (!this.jz_order) {
        Toast("请选择所在学部");
      } else {
        this.$router.push({ path: "/history", query: {} });
      }
    },

    
    
  },
};
</script>

