import Vue from 'vue';
import {post,get,patch,put,del} from './index'
Vue.prototype.$post=post;
Vue.prototype.$get=get;
Vue.prototype.$patch=patch;
Vue.prototype.$put=put;
Vue.prototype.$del=del;



// 请求实名信息
const userApi = 'cybook/book/queryUserProfile';
export const userInfo  = function() {
  return Vue.prototype.$get(userApi)
};

//城镇列表
const townApi = 'jz/book/town';
export const townList  = function() {
  return Vue.prototype.$get(townApi)
};

// 根据镇区名称（非必填）查询采样机构
const areaApi = 'jz/book/mechanism';
export const areaList  = function() {
  return Vue.prototype.$get(areaApi)
};

// 根据日期查询该机构时间段以及可预约量、剩余可预约量
const limitApi = 'jz/book/timeSlot';
export const limitData  = function(params) {
  return Vue.prototype.$get(limitApi,{"date_time":params.date_time,'org_id':params.org_id})
};

// 提交预约
const submitApi = 'jz/book/submitAppoint';
export const submitData   = function(params) {
  return Vue.prototype.$post(submitApi,{
    "id_card":params.id_card ,
    'user_name':params.user_name,
    'mobile':params.mobile,
    'jz_org':params.jz_org,
    'book_date':params.book_date,
    "period":params.period,
    'town':params.town,
    'jz_order':params.jz_order,
    'remark':params.remark,
  })
};



// 预约记录
const orderApi = 'jz/book/bookRecord';
export const orderList  = function() {
  return Vue.prototype.$get(orderApi )
};

// 取消预约
const cancelApi = 'jz/book/cancelBook';
export const setCancel  = function(params) {
  return Vue.prototype.$post(cancelApi,{book_id:params.book_id})
};

